@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  * {
    @apply border-border;
  }
  
  body {
    @apply bg-background text-foreground;
    font-feature-settings: "rlig" 1, "calt" 1;
  }
}

@layer components {
  .card-gradient {
    @apply bg-gradient-to-br from-zinc-900/80 to-zinc-800/80 backdrop-blur-sm;
  }
  
  .glass-effect {
    @apply bg-zinc-900/80 backdrop-blur-sm border border-zinc-700/50;
  }
  
  .status-badge {
    @apply inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium;
  }
}
